let IS_NODE = false;
let IS_UITEST = false;
const UITEST_FLAG = 'uitest';

try {
  IS_NODE = !!process.versions.node;
} catch (e) {
  // do nothing
}

try {
  IS_UITEST = process.env.RUNTIME_ENV === UITEST_FLAG;
} catch (e) {
  // do nothing
}
  
export {
  IS_NODE,
  IS_UITEST,
  UITEST_FLAG,
};

export const TOOLBAR_WIDTH_FOR_UITEST = 9999;